import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-i-transfer05',
  templateUrl: 'i-transfer05.html',
})
export class ITransfer05Page {

  initTransferResponse ;
  totalDeduction ;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public gaProvider : GoogleAnalyticsProvider) {

    this.initTransferResponse = JSON.parse(this.navParams.get("initTransferResponse"));
    this.totalDeduction = this.navParams.get("totalDeduction");
  }

  goToWelcome = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.popToRoot();
  };

  transferAnother = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.push('iTransfer01');
  };

  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("iTransfer Successful");
  }

}



